import React, { useState, useEffect } from "react";
import "./App.css";
import axios from "axios";
import header from "./COT_1_2023_header.png";
import footerImage from "./COT_2_2023_footer.png";
import Select from 'react-select';


type data = { Name: string, Club: string, Division: string, Number: number }
type areaDropdown = { value: string, label: string}

const options = [
  { value: 'chocolate', label: 'Chocolate' },
  { value: 'strawberry', label: 'Strawberry' },
  { value: 'vanilla', label: 'Vanilla' },
];

function App() {
  const [registrationId, setRegistrationId] = useState("");
  const [title, setTitle] = useState("");
  const [name, setName] = useState("")
  const [showSuccess, setShowSuccess] = useState(false);
  const [showSuccessMessage, setShowSuccessMessage] = useState(false);
  const [isSubmit, setIsSubmit] = useState(false);
  const [dataList, setDataList] = useState<Map<string, data[]>>();


  const [selectedAreaOption, setSelectedAreaOption] = useState(null);
  const [areaDropdown, setAreaDropdown] = useState<areaDropdown[]>()

  const [selectedMemberOption ,setSelectedMemberOption] = useState(null)
  const [memberDropdown, setMemberDropdown] = useState<areaDropdown[]>()



  useEffect(() => {
    (async () => {
      const url = "https://script.google.com/macros/s/AKfycbzRkxv-_bHpEdQKsAoLrFXQxjt1X3OoiN0VVWHH8rtBflqiT_MgTlriWs59HVXVw1Y/exec"
      const response = await axios.get(url)
      if (response.status === 200) {
        const dataList = response.data.dataList
        const dataMapList = new Map<string, data[]>()
        dataList.forEach((data: data) => {
          if (dataMapList.has(data.Division)) {
            const divisionList = dataMapList.get(data.Division);
            divisionList && divisionList?.push(data)
          } else {
            dataMapList.set(data.Division, [data])
          }
        })
        const keys = Array.from(dataMapList.keys())
        const options :areaDropdown[] = [];
        keys.forEach(key =>{
          options.push({ value: key, label: key })
        })
          setAreaDropdown(options)
          setDataList(dataMapList)
      }
    })()
  }, [])
  const inputOnChange = (e: any) => {
    setRegistrationId(e);
  };

  const resetForm = () => {
    setName('')
    setShowSuccessMessage(false)
  }

  const submitRequest = async () => {
    setIsSubmit(true);
    const url = "https://script.google.com/macros/s/AKfycbyiMwzgjuOFUhuNDJfYmHwJC6LHZjtqlGzEFxmajwoYVY9QvVoKqzlgI-DHmmf1yQ/exec"
    const response = await axios.post(
      url,
      { id: Number(registrationId) },
      {
        headers: {
          "Content-Type": "text/plain;charset=utf-8",
        },
      }
    );
    if (response.status === 200) {
      const statusCode = response.data.statusCode;
      if (statusCode === "202") {
        setTimeout(() => {
          setShowSuccessMessage(true)
          setShowSuccess(true);
          setIsSubmit(true);
          setName(response.data.name)
        }, 0);
      } else if (statusCode === "208") {
        setTitle("Already Register !");
      } else if (statusCode === "404") {
        setTitle("Record not found !");
      }
    }
    setTimeout(() => {
      setTitle("");
      setIsSubmit(false);
      setRegistrationId("");
      setShowSuccess(false);
    }, 3000);
  };

  const selectArea =(value:any)=>{
   const memberList = dataList?.get(value.value)
   const _selectedAreaMemberList :any =[]
   memberList?.forEach(d=>{
    _selectedAreaMemberList.push({ 'label' : d.Name + " |  | " + d.Club , 'value' : d.Number})
   })
   setMemberDropdown(_selectedAreaMemberList)
   console.log(_selectedAreaMemberList)
  }

  const selectMember = (value:any) =>{
    inputOnChange(value.value)
  }

  return (
    <div className="container mx-auto grid min-h-screen bg-dark-green">
      <div className="grid justify-items-center">
        <img src={header} alt="headerImage" />
      </div>
      <div className="grid my-12 grid-cols-6 gap-4 ">
        {
          !showSuccessMessage ?
            <>
              <div className="col-start-2 col-span-4">
                <span className="text-white font-bold">{title}</span>
                <div className="flex flex-col">
                  {/* <input
                    className="border-4 lowercase my-4 h-12 border-bt-bg-color"
                    onChange={(e) => inputOnChange(e.target.value)}
                    value={registrationId}
                    type="number"
                  /> */}
                  <span className="text-white font-bold">Select Area</span>
                  <Select
                   className="border-4 my-4 h-12 border-bt-bg-color"
                    defaultValue={selectedAreaOption}
                    onChange={(value)=>selectArea(value)}
                    options={areaDropdown}
                  />
                  <span className="text-white font-bold">Select Member</span>
                <Select
                 className="border-4 my-4 h-12 border-bt-bg-color"
                    defaultValue={selectedMemberOption}
                    onChange={(value)=>selectMember(value)}
                    options={memberDropdown}
                  />
                  <div className="flex justify-center">
                    <button
                      className="text-bt-text-color font-bold w-auto py-2 px-4 m-4 rounded bg-bt-bg-color"
                      type="button"
                      disabled={isSubmit}
                      onClick={() => submitRequest()}
                    >
                      {isSubmit ? (
                        <div className="loader1">
                          <span></span>
                          <span></span>
                          <span></span>
                          <span></span>
                          <span></span>
                        </div>
                      ) : (
                        <>Submit</>
                      )}
                    </button>
                  </div>
                </div>
              </div>
            </>
            :
            <>
              <div className="col-start-2 col-span-6">
                <div className="my-2">
                  <span className="text-white font-bold">{'Welcome "' + name + '"'}</span>
                </div>
              </div>
              <div className="col-start-2 col-span-4 -my-4 ">
                <div className="flex justify-center">
                  <button
                    className="text-bt-text-color font-bold w-auto py-2 px-4 m-4 rounded bg-bt-bg-color"
                    type="button"
                    onClick={() => resetForm()}
                  >
                    Refresh
                  </button>
                </div>
              </div>
            </>
        }
      </div>

      <div>
        <div className="grid justify-items-center">
          <a style={{ color: 'white' }} href="https://github.com/maksha19">Powered by @maksha</a>
          <img src={footerImage} alt="footerImage" />
        </div>
        {showSuccess && (
          <a href="/">
            <div className="bg-white">
              <div className="container">
                <div className="action">
                  <div className="trophy">
                    <svg
                      fill="#f9bc60"
                      width="100%"
                      height="100%"
                      viewBox="0 0 24 24"
                    >
                      <path d="M12,2A10,10 0 0,1 22,12A10,10 0 0,1 12,22A10,10 0 0,1 2,12A10,10 0 0,1 12,2M11,16.5L18,9.5L16.59,8.09L11,13.67L7.91,10.59L6.5,12L11,16.5Z"></path>
                    </svg>
                  </div>
                  <div className="confetti"></div>
                  <div className="confetti two"></div>
                  <div className="confetti three"></div>
                  <div className="confetti four"></div>
                  <div className="confetti--purple"></div>
                  <div className="confetti--purple two"></div>
                  <div className="confetti--purple three"></div>
                  <div className="confetti--purple four"></div>
                </div>
              </div>
            </div>
          </a>
        )}
      </div>
    </div>
  );
}

export default App;
